@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  *,
  *::after,
  *::before {
    @apply box-border z-0 m-0 p-0;
  }
  h1 {
    @apply block text-3xl font-bold;
  }
  h2 {
    @apply block text-3xl font-bold;
  }
  h3 {
    @apply block text-2xl font-bold;
  }
  hr {
    @apply block box-border text-gray-600;
  }
  ul {
    @apply block list-disc mt-4 mb-4;
  }
  ol {
    @apply block list-decimal mt-4 mb-4;
  }
  table {
    @apply table mt-2 mb-2 w-full;
  }
  td {
    @apply border-2 border-txmain p-3;
  }
}
body {
  @apply bg-steel-900 text-txmain relative w-screen;
}
@media (min-width: 401px) {
  body {
    @apply w-full;
  }
}

img[src$="?display=inline"] {
  @apply inline;
}

main {
  @apply grid relative;
  margin-top: 13.5vh;
}
@media (min-width: 972px) {
  main {
    @apply mt-0;
  }
}

.header {
  @apply hidden w-full;
}
@media (min-width: 972px) {
  .header {
    @apply grid border-b-1.5 sticky;
  }
}
.header__link {
  @apply grid grid-flow-col;
  grid-auto-columns: max-content auto;
}
.header__logo-img {
  width: 340.188px;
  height: 268.469px;
}
.header__title {
  @apply w-max flex flex-col justify-center text-2xl;
}
.header__span-title {
  @apply text-xl;
}
.header__menu-list {
  @apply grid grid-flow-col list-none p-1 m-0;
  grid-auto-columns: max-content;
}
.header__menu-list-element {
  @apply p-3 border-r-1.5 border-txmain transition-colors duration-200 ease-in-out;
}
.header__menu-list-element--first {
  @apply border-l-1.5;
}
.header__menu-list-element:hover {
  @apply bg-txmain text-steel-900;
}
.header__menu {
  @apply border-t-1.5 border-txmain p-0.5 pl-6 pr-6;
}

.phone-header {
  @apply grid-cols-3/4 bg-steel-900 fixed top-0 z-10 grid w-full grid-flow-col border-b-2 border-white max-h-28 min-h-58.5;
}
@media (min-width: 972px) {
  .phone-header {
    @apply hidden;
  }
}
.phone-header__title {
  @apply flex flex-wrap content-center text-xs max-h-28 max-w-18.5;
}
@media (min-width: 300px) {
  .phone-header__title {
    @apply text-sm;
  }
}
@media (min-width: 455px) {
  .phone-header__title {
    @apply text-lg;
  }
}
@media (min-width: 600px) {
  .phone-header__title {
    @apply text-xl;
  }
}
.phone-header__span {
  @apply block text-xxs;
}
@media (min-width: 300px) {
  .phone-header__span {
    @apply text-xs;
  }
}
@media (min-width: 455px) {
  .phone-header__span {
    @apply text-base;
  }
}
@media (min-width: 600px) {
  .phone-header__span {
    @apply text-lg;
  }
}
.phone-header__logo-content {
  @apply grid grid-flow-col gap-1 h-full;
}
.phone-header__burger-menu {
  @apply grid content-center h-full pb-2 max-w-32;
}
@media (min-width: 465px) {
  .phone-header__burger-menu {
    @apply pr-3;
  }
}
@media (min-width: 513px) {
  .phone-header__burger-menu {
    @apply pr-0;
  }
}
.phone-header__burger-elements {
  @apply border-1.5 border-txmain w-3/5 justify-self-center bg-txmain mt-2 border-solid rounded-full;
}
@media (min-width: 400px) {
  .phone-header__burger-elements {
    @apply w-4/5 border-2;
  }
}
@media (min-width: 465px) {
  .phone-header__burger-elements {
    @apply justify-self-end w-1/2;
  }
}
@media (min-width: 513px) {
  .phone-header__burger-elements {
    @apply w-4/5 justify-self-center;
  }
}
@media (min-width: 513px) {
  .phone-header__burger-container {
    @apply w-full h-full grid justify-items-end items-center;
  }
}
.phone-header__burger-link {
  @apply w-full h-full;
}
@media (min-width: 513px) {
  .phone-header__burger-link {
    @apply w-22 h-18;
  }
}
.phone-header__img {
  @apply min-w-0.5 self-center max-h-28;
}
.phone-header__logo {
  @apply max-w-sm;
}

.phone-menu {
  @apply bg-steel-900 fixed top-0 right-0 z-10 w-2/5 h-full overflow-auto transform translate-x-full border-l-2 border-white;
  margin-top: 58.5px;
}
@media (min-width: 274px) {
  .phone-menu {
    margin-top: 21.8vw;
  }
}
@media (min-width: 542px) {
  .phone-menu {
    margin-top: 12.05vh;
  }
}
@media (min-width: 972px) {
  .phone-menu {
    @apply hidden;
  }
}
.phone-menu__list {
  @apply mt-0 text-center list-none;
}
.phone-menu__list-element {
  @apply h-min p-2 border-b-2 border-white;
}

.title-container {
  @apply h-98 justify-items-center grid grid-rows-2 p-1 w-screen;
}
@media (min-width: 401px) {
  .title-container {
    @apply w-auto;
  }
}
@media (min-width: 972px) {
  .title-container {
    width: 100%;
    height: 540px;
  }
}
.title-container__title {
  @apply self-end row-start-1 m-0 text-center;
}
@media (min-width: 497px) {
  .title-container__title {
    @apply w-4/5;
  }
}
@media (min-width: 638px) {
  .title-container__title {
    @apply text-4xl;
  }
}
@media (min-width: 759px) {
  .title-container__title {
    @apply w-3/5;
  }
}
@media (min-width: 1005px) {
  .title-container__title {
    @apply max-w-xl;
  }
}
.title-container__btn {
  @apply animate-bounce self-end;
}

.section-articles {
  @apply border-t-3 grid gap-6 p-2 pt-3 pb-3 mt-3 border-white;
}
@media (min-width: 690px) {
  .section-articles {
    @apply gap-3;
  }
}
.section-articles__link-article-container {
  @apply grid justify-start w-full shadow-2xl h-48 p-2 border-1.5 border-txmain rounded-lg;
}
@media (min-width: 690px) {
  .section-articles__link-article-container {
    @apply w-2/3;
  }
}
@media (min-width: 850px) {
  .section-articles__link-article-container {
    @apply w-3/5;
  }
}
@media (min-width: 1400px) {
  .section-articles__link-article-container {
    width: 55%;
  }
}
.section-articles__link-article-container--right {
  @apply justify-self-end justify-end;
}
.section-articles__link-article-container--sources {
  @apply gap-3;
}
.section-articles__link {
  @apply justify-self-start h-min self-end;
}
.section-articles__link--right {
  @apply justify-self-end;
}
@media (min-width: 972px) {
  .section-articles__link:hover {
    @apply animate-pulse;
  }
}
.section-articles__title-link {
  @apply text-3xl;
}
.section-articles__span {
  @apply text-lg;
}
.section-articles__span--right {
  @apply justify-self-end;
}
.section-articles__img {
  @apply max-w-none w-13;
}
.section-articles__title-container {
  @apply grid;
}

.footer__link {
  @apply grid grid-cols-2 justify-center items-center gap-1 p-1 border-t-3 border-txmain;
}
.footer__logo {
  @apply w-24 justify-self-end max-w-44;
}
@media (min-width: 497px) {
  .footer__logo {
    @apply w-1/2;
  }
}
.footer__title {
  @apply max-w-80;
}
@media (min-width: 497px) {
  .footer__title {
    @apply text-lg;
  }
}
@media (min-width: 575px) {
  .footer__title {
    @apply text-2xl;
  }
}

.navigation-aside {
  @apply relative z-10 w-full mb-2;
}
@media (min-width: 972px) {
  .navigation-aside {
    @apply border-none;
  }
}
.navigation-aside__list {
  @apply m-0 p-2 pl-10 bg-steel-900 border-b-1.5 border-txmain hidden;
}
@media (min-width: 972px) {
  .navigation-aside__list {
    @apply grid list-none border-1.5 rounded-lg pl-2;
  }
}
.navigation-aside__list-element {
  @apply p-1;
}
.navigation-aside__list-element--clicked {
  @apply text-steel-400;
  filter: drop-shadow(-3px 6px 4px rgba(0, 0, 0, 0.25));
}
.navigation-aside__open-btn {
  @apply grid grid-flow-col gap-1 p-2 bg-steel-900 w-full relative z-20;
  grid-auto-columns: max-content;
}
@media (min-width: 972px) {
  .navigation-aside__open-btn {
    @apply hidden;
  }
}
.navigation-aside__img {
  @apply w-6 h-6;
}
@media (min-width: 972px) {
  .navigation-aside > hr {
    @apply hidden;
  }
}

.article-and-menu {
  @apply flex justify-center flex-wrap border-t-3 border-txmain gap-2 w-screen;
}
@media (min-width: 401px) {
  .article-and-menu {
    @apply w-auto;
  }
}
@media (min-width: 972px) {
  .article-and-menu {
    @apply grid grid-flow-col p-3;
  }
}

.article-container {
  @apply flex justify-center flex-wrap p-2 mb-3 w-full relative text-xl overflow-auto;
}
@media (min-width: 610px) {
  .article-container {
    @apply w-4/5 shadow-3xl;
  }
}
@media (min-width: 830px) {
  .article-container {
    @apply w-3/5;
  }
}
@media (min-width: 972px) {
  .article-container {
    width: 577px;
  }
}
@media (min-width: 1275px) {
  .article-container {
    width: 877px;
  }
}
@media (min-width: 1700px) {
  .article-container {
    width: 1077px;
  }
}
.article-container > h2 {
  @apply w-full mb-3;
}
.article-container > p {
  @apply w-full mt-2;
}
.article-container > h3 {
  @apply w-full mt-2;
}
.article-container a {
  @apply text-plum-500 underline transition-colors w-full;
}
@media (min-width: 972px) {
  .article-container a:hover {
    @apply text-txmain;
  }
}
.article-container li {
  @apply mt-2;
}
.article-container img[src$="?display=inline"] {
  @apply mt-0;
}
.article-container ul {
  @apply ml-3;
}
.article-container ol {
  @apply ml-3;
}

.buttonTop {
  @apply fixed bottom-2 right-2 p-2 bg-txmain rounded-full border-steel-900 border-2 transition-opacity hidden opacity-0;
}
@media (min-width: 972px) {
  .buttonTop:hover {
    @apply opacity-75;
  }
}

.loaderBox {
  @apply absolute w-full h-full justify-items-center z-10 bg-steel-900 hidden content-center gap-2;
}
.loaderBox__loader {
  @apply animate-spin h-18;
}
.loaderBox__span {
  @apply grid justify-items-center;
}
.loaderBox__btn {
  @apply text-plum-500 underline transition-colors;
}
@media (min-width: 972px) {
  .loaderBox__btn:hover {
    @apply text-txmain;
  }
}